'use client';
import { createContext, useContext, useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { setCookie, deleteCookie } from 'cookies-next';

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const router = useRouter();

  // Vérifie l'état d'authentification au chargement
  useEffect(() => {
    const checkAuth = async () => {
      try {
        const res = await fetch('http://localhost:3003/api/users/profile', {
          credentials: 'include'
        });
        if (res.ok) {
          setUser(await res.json());
        }
      } catch (error) {
        console.error("Erreur de vérification d'authentification:", error);
      } finally {
        setLoading(false);
      }
    };
    checkAuth();
  }, []);

  const login = async (email: string, password: string) => {
    const res = await fetch('http://localhost:3003/auth/login', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ email, password }),
      credentials: 'include'
    });
    
    if (!res.ok) throw new Error('Échec de la connexion');
    
    const userData = await res.json();
    setUser(userData);
    router.push('/');
  };

  const register = async (username: string, email: string, password: string) => {
    const res = await fetch('http://localhost:3003/api/auth/register', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ username, email, password }),
      credentials: 'include'
    });
    
    if (!res.ok) throw new Error('Échec de l\'inscription');
    
    await login(email, password);
  };

  const logout = async () => {
    await fetch('http://localhost:3003/api/auth/logout', {
      method: 'POST',
      credentials: 'include'
    });
    setUser(null);
    deleteCookie('token');
    router.push('/auth/login');
  };

  return (
    <AuthContext.Provider value={{ user, loading, login, register, logout }}>
      {children}
    </AuthContext.Provider>
  );
}